.wrapper {
  padding: 1rem;
  display: flex;
  flex-wrap: nowrap;
  justify-content: center;
  gap: 1rem;
  overflow: hidden;
  position: relative;
}
.card {
  flex: none;
  width: 20rem;
  height: 20rem;
  background: #fff;
  border-radius: 1rem;
  box-shadow: 0 0 .5rem #395b64;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: relative;
  left: -120%;
  animation: 2s rolling forwards;
}

.card img {
  width: 90%;
  border-radius: 1rem;
}

.title {
  font-size: 1.6rem;
  margin: .5rem;
}

.description {
  font-size: 1.2rem;
  margin: .5rem;
}

@keyframes rolling {
  from {left: -120%; transform: rotate(0deg); scale: .1}
  to {left: 0; transform: rotate(360deg); scale: 1}
}